/**
 * =============================================================================
 * ************   Menu    ************
 * =============================================================================
 */

// 
@menu-simple-x: 56px;

// 
@menu-cascade-x: 64px;

/*  */
.mdui-menu {
  box-sizing: border-box;
  padding: 8px 0;
  margin: 0;
  font-size: 16px;
  .mdui-shadow(8);
  border-radius: 2px;
  width: @menu-simple-x * 3;
  background-color: #fff;
  color: @color-black-text;
  list-style: none;
  display: block;
  z-index: 99999;
  opacity: 0;
  will-change: transform, opacity, visibility;
  transition-property: transform, opacity, visibility;
  transition-duration: .3s;
  transition-timing-function: @animation-curve-linear-out-slow-in;

  visibility: hidden;
  transform: scale(0);
  position: fixed;

  // 
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  //  8px 
  .mdui-divider {
    margin-top: 8px;
    margin-bottom: 8px;
  }
}

/*  */
.mdui-menu-open {
  visibility: visible;
  transform: scale(1);
  opacity: 1;
}

/*  */
.mdui-menu-closing {
  visibility: visible;
  transform: scale(1);
  opacity: 0;
}

/*  */
.mdui-menu-item {
  position: relative;

  &>a {
    position: relative;
    display: block;
    color: inherit;
    text-decoration: none;
    height: 48px;
    line-height: 48px;
    padding: 0 16px;
    user-select: none;
    .mdui-text-truncate();

    &:hover {
      background-color: @color-grey-200;
    }
  }

  // 
  &>.mdui-menu {
    position: absolute;
    .mdui-shadow(9);
  }

  // 
  &[disabled] {
    &>a {
      cursor: default;
      color: @color-black-disabled !important;

      &:hover {
        background-color: inherit !important;
      }

      .mdui-icon {
        color: @color-black-icon-disabled;
      }
    }
  }
}

/*  */
.mdui-menu-item-active {
  background-color: @color-grey-200;
}

/*  */
.mdui-menu-item-icon {
  color: @color-black-icon;
  width: 40px;
  padding-right: 16px;
  box-sizing: border-box;
  display: inline-block;
}

/*  */
.mdui-menu-item-helper {
  float: right;
}

/*  */
.mdui-menu-item-more {
  float: right;
  width: 24px;
  height: 24px;
  margin: 4px 0;
  background-image: data-uri('image/svg+xml', './menu/svg/arrow_right_dark.svg');
}


/*  */
.mdui-menu-cascade {
  padding: 16px 0;
  font-size: 15px;
  width: @menu-cascade-x * 5;

  // 
  overflow-y: visible;

  &>.mdui-menu-item {
    &>a {
      height: 32px;
      line-height: 32px;
      padding: 0 24px;
    }
  }
}


/**
 * =============================================================================
 * ************   Menu dark   ************
 * =============================================================================
 */
.mdui-theme-layout-dark when (@globalLayoutDark = true) {
  .mdui-menu {
    background-color: @layout-dark-color-4;
    color: #fff;
  }

  .mdui-menu-item {
    &>a {
      &:hover {
        background-color: @color-grey-700;
      }
    }

    &[disabled] {
      &>a {
        color: @color-white-disabled !important;

        .mdui-icon {
          color: @color-white-icon-disabled;
        }
      }
    }
  }

  .mdui-menu-item-active {
    background-color: @color-grey-700;
  }

  .mdui-menu-item-icon {
    color: @color-white-icon;
  }

  .mdui-menu-item-more {
    background-image: data-uri('image/svg+xml', './menu/svg/arrow_right_light.svg');
  }
}
