.mdui-overlay {
  position: fixed;
  z-index: 2000;
  top: -5000px;
  right: -5000px;
  bottom: -5000px;
  left: -5000px;
  backface-visibility: hidden;
  background: rgba(0, 0, 0, 0.4);
  will-change: opacity;
  opacity: 0;
  visibility: hidden;
  transition-property: opacity, visibility;
  transition-duration: 0.3s;
}
/*  */
