/*  */
.mdui-fab {
  border-radius: 50%;
  font-size: 24px;
  margin: auto;
  padding: 0 !important;
  overflow: hidden;
  line-height: normal !important;
  box-shadow: 0 3px 5px -1px rgba(0, 0, 0, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12);
  height: 56px;
  width: 56px;
  min-width: 56px;
}
.mdui-fab:hover {
  box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2), 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12);
}
.mdui-fab:active {
  box-shadow: 0 7px 8px -4px rgba(0, 0, 0, 0.2), 0 12px 17px 2px rgba(0, 0, 0, 0.14), 0 5px 22px 4px rgba(0, 0, 0, 0.12);
}
.mdui-fab .mdui-icon {
  position: absolute;
  top: 0;
  left: 0;
  line-height: 24px;
  width: 24px;
  margin-left: 16px;
  margin-top: 16px;
}
/* mini  */
.mdui-fab-mini {
  height: 40px;
  width: 40px;
  min-width: 40px;
}
.mdui-fab-mini .mdui-icon {
  margin-left: 8px;
  margin-top: 8px;
}
/*  FAB  */
.mdui-fab-fixed,
.mdui-fab-wrapper {
  position: fixed !important;
  /*  16px */
  right: 16px;
  bottom: 16px;
}
