  .mdui-fab-fixed,
  .mdui-fab-wrapper {
    /*  24px */
    right: 24px;
    bottom: 24px;
  }
}
/*  */
.mdui-fab-wrapper {
  position: relative;
  width: 56px;
  height: 56px;
  padding-top: 8px;
  z-index: 4000;
  text-align: center;
}
.mdui-fab-wrapper > .mdui-fab .mdui-icon:not(.mdui-fab-opened) {
  will-change: opacity, transform;
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  opacity: 1;
}
.mdui-fab-wrapper > .mdui-fab .mdui-icon.mdui-fab-opened {
  will-change: opacity, transform;
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  opacity: 0;
  transform: rotate(225deg);
}
.mdui-fab-wrapper > .mdui-fab.mdui-fab-opened .mdui-icon:not(.mdui-fab-opened) {
  opacity: 0;
  transform: rotate(225deg);
}
.mdui-fab-wrapper > .mdui-fab.mdui-fab-opened .mdui-icon.mdui-fab-opened {
  opacity: 1;
  transform: rotate(360deg);
}
.mdui-fab-wrapper > .mdui-fab-mini {
  margin-top: 8px;
}
.mdui-fab-wrapper .mdui-fab-dial {
  visibility: visible;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 64px;
  text-align: center;
  height: 0;
}
.mdui-fab-wrapper .mdui-fab-dial .mdui-fab {
  margin: 8px 0;
  transition: box-shadow 0.2s cubic-bezier(0.4, 0, 1, 1), color 0.2s cubic-bezier(0.4, 0, 0.2, 1), transform 150ms cubic-bezier(0.4, 0, 0.2, 1), opacity 150ms cubic-bezier(0.4, 0, 0.2, 1);
  transform: scale(0);
  opacity: 0;
}
.mdui-fab-wrapper .mdui-fab-dial.mdui-fab-dial-show .mdui-fab {
  transform: scale(1);
  opacity: 1;
}
/*  .mdui-fab-hide  */
.mdui-fab,
.mdui-fab-mini,
.mdui-fab-wrapper {
  will-change: transform;
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1), box-shadow 0.2s cubic-bezier(0.4, 0, 1, 1), transform 0.2s;
  transform: scale(1) translateZ(0);
}
.mdui-fab.mdui-fab-hide,
.mdui-fab-mini.mdui-fab-hide,
.mdui-fab-wrapper.mdui-fab-hide {
  transform: scale(0) translateZ(0);
}
