/* DOM  transition */
body.mdui-loaded {
  transition: padding 0.3s cubic-bezier(0, 0, 0.2, 1);
}
body.mdui-loaded .mdui-drawer {
  transition: all 0.3s cubic-bezier(0, 0, 0.2, 1);
