 */
.mdui-bottom-nav {
  display: flex;
  position: relative;
  overflow: hidden;
  height: 56px;
  padding: 0;
  margin: 0 auto;
  white-space: nowrap;
}
@media (min-width: 600px) {
  .mdui-bottom-nav:before {
    content: ' ';
    flex-grow: 1;
  }
  .mdui-bottom-nav:after {
    content: ' ';
    flex-grow: 1;
  }
}
.mdui-bottom-nav a {
  display: flex;
  color: inherit;
  text-decoration: none;
  font-size: 12px;
  text-align: center;
  cursor: pointer;
  text-overflow: ellipsis;
  overflow: hidden;
  user-select: none;
  opacity: 0.7;
  min-width: 32px;
  max-width: none;
  padding: 8px 12px 10px 12px;
  flex: 1;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  will-change: padding;
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
